@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.navbar {
    background-color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    padding: 20px 0;
}
.navbar-item-text {
    color: #8D81AC;
    text-decoration: none;
    padding: 19px 25px;
    transition: 0.5s;
}
.navbar-item {
    list-style: none;
    font-size: 18px;
    font-family: 'Balsamiq Sans';
}

.navbar-item-text:hover {
    color:#fff;
}
.profile-photo {
    width: 300px;
    height: 400px;

}

.profile-header {
    margin-top: 30px;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: 'Balsamiq Sans';
}
.profile-list {
    margin-top: 50px;
}
.profile-information {
    padding-left: 30px;
    padding-right: 15px;
    background-color: #E5E1E6;
    width: 500px;
    margin-right: 50px;
}
.profile-field {
    font-size: 16px;
    margin-top: 5px;
    font-family: 'Balsamiq Sans';
}
.profile-description {
    margin-top: 25px;
    font-size: 14px;
    line-height: 25px;
    font-family: 'Balsamiq Sans';
}
.skill-point {
    background: #456BD9;
    border: 1px solid #0B1B81;
    border-radius: 50%;
    box-shadow: 3px 3px 0 0 rgba(15, 28,129, 0.125);
    height: 35px;
    width: 35px;
    margin-left: 30px;
}
.skill-point-active{
    background: #DF1F1F;
    border-color: #910A0A;
    -webkit-animation: pulsing 7s infinite;
            animation: pulsing 7s infinite;
}
.skill-item {
    margin-top: 40px;
    display: flex;
    align-items: center;
    position: relative;
}
.skill-name {
    font-size: 24px;
    margin-left: 70px;
    font-family: 'Balsamiq Sans';
    text-transform: uppercase;
    width: 150px;
}
.skill-item::after {
    content: "";
    position: absolute;
    bottom: -11px;
    width: 100%;
    display: block;
    border-bottom: 2px solid gray;
}
.skill-score {
    display: flex;
}

.skills-header {
    margin-top: 50px;
    font-size: 30px;
    margin-left: 70px;
    font-family: 'Balsamiq Sans';
    font-weight: 700;
}
@-webkit-keyframes pulsing {
    0% {
        transform: scale(1.3, 1.3)
    }
    50% {
        transform: scale(1.0, 1.0);
    }
    70% {
        transform: scale(1.0, 1.0);
    }
    100% {
        transform: scale(1.3, 1.3);
    }
}
@keyframes pulsing {
    0% {
        transform: scale(1.3, 1.3)
    }
    50% {
        transform: scale(1.0, 1.0);
    }
    70% {
        transform: scale(1.0, 1.0);
    }
    100% {
        transform: scale(1.3, 1.3);
    }
}
.container {
    width: 1180px;
    margin: 0 auto;
}
.profile {
    display: flex;
    justify-content: center;
}
.home-page {
    padding: 50px 0;
}
.project-name {
    margin-top: 40px;
    font-size: 24px;
    font-family: 'Balsamiq Sans';
    text-transform: uppercase;
    width: 50%;
}
.project-data {
    width: 50%;
    text-align: right;
    font-size: 17px;
    font-family: 'Balsamiq Sans';
}
.project-information {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    width: 100%;
}
.project-description {
    margin-top: 20px;
    font-size: 16px;
    font-family: 'Balsamiq Sans';
    width: 100%;
}
.project-link {
    margin-top: 25px;
